import * as Yup from 'yup';

export default Yup.object().shape({
  referenceName: Yup.string().required('O nome de referencia  obrigatrio'),
  client: Yup.string().required('Selecione um cliente !!'),
  maintenances: Yup.array().of(
    Yup.object().shape({
      description: Yup.string().required('A descrio  obrigatria !!'),
      state: Yup.number()
    }).required('on')
  ).required('Este campo  obrigatrio !!'),
});
