import * as Yup from 'yup';

export default Yup.object().shape({
  cnpj: Yup.string().required('O CNPJ  obrigatrio !!'),
  comercialName: Yup.string().required('O nome domercial  obrigatrio !!'),
  contact: Yup.string(),
  phone: Yup.string().required('Ao menos um contato  necessrio !!'),
  address: Yup.object().shape({
    street: Yup.string().required('A rua  obrigatria !!'),
    postcode: Yup.string().required('O cep  obrigatrio !!'),
    state: Yup.string().required('O estado  obrigatria !!'),
    city: Yup.string().required('A cidade  obrigatria !!'),
    number: Yup.number().typeError('O tipo deve ser um numero, caso no tenha digite 0.').required('O nmero  obrigatrio !!'),
  }),
  ete: Yup.object().shape({
    model: Yup.string().required('O modelo  obrigatrio !!'),
    manufacturer: Yup.string().required('O fabricante  obrigatrio !!'),
  }),
});
